import { Module } from "@nestjs/common";
import { ConfigModule } from "@nestjs/config";
import { NatMicroServiceModule } from "../nat-service";
import { JwtAuthMicroServiceStrategy } from '../jwt/strategy/auth-micro-service.strategy';
import { JwtAuthRoleMicroServiceStrategy } from '../jwt/strategy/auth-role-micro-service.strategy';
import { AuthMicroServiceGuard } from "./guards/auth-micro-service.guard";
import { AuthRoleMicroServiceGuard } from "./guards/auth-role-micro-service.guard";

@Module({  
  imports: [
    NatMicroServiceModule
  ],
  providers: [
    {
      provide: "APP_GUARD",
      useClass: AuthMicroServiceGuard,
    },
    {
      provide: "APP_GUARD",
      useClass: AuthRoleMicroServiceGuard,
    },
    JwtAuthMicroServiceStrategy,
    JwtAuthRoleMicroServiceStrategy,
    ConfigModule
  ],
  exports: [
    JwtAuthRoleMicroServiceStrategy,
    JwtAuthMicroServiceStrategy
  ]
})
export class AuthGuardMicroServiceModule {}